import React, { Component } from 'react'
import {Base_URL,headers, imgPath} from '../../CommonRoutes/Api'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CInput,
  CSelect,
  CInputGroup,
  CRow
} from '@coreui/react'
import axios from 'axios';
import "../../scss/Wallet.scss";
import {faPen,faTrash} from '@fortawesome/free-solid-svg-icons';
import {ToastsStore} from 'react-toasts';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { getProduct,getCategory,getSubCategorybyID } from 'src/CommonRoutes/Constant';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';


export default class Product extends Component {
    constructor(props){
        super(props);
        this.state={
            proscreen:true,proadd:false,proedit:false,eid:'',
            product_price:'',product_desc:'',product_name:'',product_oldprice:'',product_image:'',cat_id:'',subcat_id:'',
            productList:[],catList:[],subCatList:[],
            vendor_id:localStorage.getItem("user_id"),
            blocking:false
        }
    }
    handleEdit=(id)=>{
      this.setState({proscreen:false,proadd:false,proedit:true,eid:id})
      fetch(Base_URL+"getvendor_productbyid/" + id,{ method:'GET',headers })
        .then((response) => response.json())
        .then((result) =>{
          this.setState({product_name:result.data.product_name,product_price:result.data.product_price,product_oldprice:result.data.product_oldprice,
            product_desc:result.data.product_desc,product_image:result.data.product_image})
        ToastsStore.warning("Category Updated successfully!");
        })
    }

    handleScreen=()=>{
      this.setState({proscreen:true,proadd:false,proedit:false})
    }

    handleAdd=()=>{
      this.setState({proscreen:false,proadd:true,proedit:false})
    }

    // updateProduct=()=>{
    //   let data=this.state;
    //   let url = Base_URL+"updatevendor_product/"+this.state.eid;
    //   axios.put(url, data,headers)
    //       .then(res => {
    //           console.log(res.data);
    //           if (res.data && res.data.message === "product created") {
    //               this.componentDidMount();
    //               this.handleScreen();
    //           }
    //       })
    //   .catch(err => console.log(err))
    // }

    updateProduct=()=>{
      console.log(this.state);
      let form_data = new FormData();
      form_data.append('product_image', this.state.image, this.state.image.name);
      form_data.append('product_name', this.state.product_name);
      form_data.append('product_desc', this.state.product_desc);
      form_data.append('product_price', this.state.product_price);
      form_data.append('product_oldprice', this.state.product_oldprice);
      form_data.append('vendor_id', this.state.vendor_id);
      form_data.append('cat_id', this.state.cat_id);
      form_data.append('subcat_id', this.state.subcat_id);

      let url = Base_URL+"updatevendor_product/"+this.state.eid;
      axios.put(url, form_data,headers)
          .then(res => {
              console.log(res.data);
              if (res.data && res.data.message === "product created") {
                  this.componentDidMount();
                  this.handleScreen();
              }
          })
      .catch(err => console.log(err))
    }

    handleSubmit = () => {
      console.log(this.state);
      let form_data = new FormData();
      form_data.append('product_image', this.state.image, this.state.product_image.name);
      form_data.append('product_name', this.state.product_name);
      form_data.append('product_desc', this.state.product_desc);
      form_data.append('product_price', this.state.product_price);
      form_data.append('product_oldprice', this.state.product_oldprice);
      form_data.append('vendor_id', this.state.vendor_id);
      form_data.append('cat_id', this.state.cat_id);
      form_data.append('subcat_id', this.state.subcat_id);

      let url = Base_URL+"addvendor_product";
      axios.post(url, form_data,headers)
          .then(res => {
              console.log(res.data);
              if (res.data && res.data.message === "NEW vendor_product created") {
                this.componentDidMount();
                  this.handleScreen();
              }
          })
      .catch(err => console.log(err))
  }

handleImageChange = (e) => {
  this.setState({
      image: e.target.files[0]
  })

};

handleDelet(id){
  fetch(Base_URL+"deletevendor_product/" + id,{ method:'DELETE',headers })
        .then((response) => response.json())
        .then((result) =>{
        this.componentDidMount();
        ToastsStore.warning("Category deleted successfully!");
        })
}

    componentDidMount(){
      this.setState({blocking:true})
      getProduct().then((productList) => { 
        this.setState({productList,blocking:false}); }); 

        getCategory().then((catList) => { 
          this.setState({catList,blocking:false});});
    }

    getSubCat(e){
      this.setState({cat_id:e})
      getSubCategorybyID(e).then((subCatList) => { 
        this.setState({subCatList,blocking:false}); }); 
    }
    
    render() {
        return (
           <>
{
this.state.proadd ?
  <div style={{marginTop:'-1rem'}}>
    <div className="c-app c-default-layout flex-row align-items-center" >
    <CContainer style={{padding:"10px"}}>
      <CRow className="justify-content-center">
        <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>   
            <center><h4>Add Product</h4></center>
          <CCard className="mx-10" style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.9rem', marginBottom: '-.1rem' }}>
            <CCardBody className="p-10">

                          <label style={{ color: "white" }}>Product Name</label>
                          <CInputGroup className="mb-3" >

                            <CInput type="text" name="productname" placeholder="Product Name" autoComplete="false" value={this.state.product_name}
                              onChange={(event) => { this.setState({ product_name: event.target.value }) }}
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                            />
                          </CInputGroup>

                          <label style={{ color: "white" }}>Category</label>
                          <CInputGroup className="mb-3">

                            <CSelect name="cityname" id="select"
                              onChange={(event) => { this.getSubCat(event.target.value) }}
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}>
                              <option>Select Category</option>
                              {this.state.catList.data && this.state.catList.data.length > 0 ?
                                this.state.catList.data.map((item, i) => {
                                  return <option item={item} value={item.id}>{item.CatEngName}</option>
                                }) : 'No Categories Available'}
                            </CSelect>
                          </CInputGroup>

                          <label style={{ color: "white" }}>Sub-Category</label>
                          <CInputGroup className="mb-3">

                            <CSelect name="cityname" id="select"
                              onChange={(event) => { this.setState({ subcat_id: event.target.value }) }}
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}>
                              <option>Select Sub-Category</option>
                              {this.state.subCatList.data && this.state.subCatList.data.length > 0 ?
                                this.state.subCatList.data.map((item, i) => {
                                  return <option item={item} value={item.id}>{item.SubCatEngName}</option>
                                }) : 'No city Available'}
                            </CSelect>
                          </CInputGroup>

                          <label style={{ color: "white" }}>Price</label>
                          <CInputGroup className="mb-3" >

                            <CInput type="text" name="price" placeholder="Price" autoComplete="false" value={this.state.product_price}
                              onChange={(event) => { this.setState({ product_price: event.target.value }) }}
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                            />
                          </CInputGroup>

                          <label style={{ color: "white" }}>Old Price</label>
                          <CInputGroup className="mb-3" >

                            <CInput type="text" name="oldprice" placeholder="Old Price" autoComplete="false" value={this.state.product_oldprice}
                              onChange={(event) => { this.setState({ product_oldprice: event.target.value }) }}
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                          </CInputGroup>

                          <label style={{ color: "white" }}>Description</label>
                          <CInputGroup className="mb-3">

                            <CInput type="textarea" name="desc" placeholder="desc" autoComplete="false" value={this.state.product_desc}
                              onChange={(event => { this.setState({ product_desc: event.target.value }) })}
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                            />
                          </CInputGroup>

                          <label style={{ color: "white" }}>Image</label>
                          <CInputGroup className="mb-3">
                            <CInput type="file" onChange={this.handleImageChange} name="image"
                              style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                            {/* <input type="file" onChange={this.handleImageChange} name="image"></input> */}
                          </CInputGroup>

                          <CButton type="submit" value="add" block color="primary" fullWidth={true} onClick={() => this.handleSubmit()}
                            style={{ backgroundColor: 'orange', color: "black", borderRadius: '20px' }}>save</CButton>
                          <CButton type="submit" value="add" block color="danger" fullWidth={true} onClick={() => this.handleScreen()}
                            style={{ backgroundColor: 'whitesmoke', color: "black", borderRadius: '20px' }}>Cancle</CButton>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </CContainer>
  </div>

</div>:

this.state.proedit ?
  <div style={{marginTop:'-1rem'}}>
    <div className="c-app c-default-layout flex-row align-items-center" >
    <CContainer style={{padding:"10px"}}>
      <CRow className="justify-content-center">
        <CCol md="12" lg="12" xl="12" style={{ padding: "0px" }}>
            <center><h4>Edit Product</h4></center>
            <CCard className="mx-10" style={{ backgroundColor: '#110730', marginLeft: '-1rem', marginRight: '-.9rem', marginBottom: '-.1rem' }}>
                          <CCardBody className="p-10">

                            <label style={{ color: "white" }}>Product Name</label>
                            <CInputGroup className="mb-3" >

                              <CInput type="text" name="productname" placeholder="Product Name" autoComplete="false" value={this.state.product_name}
                                onChange={(event) => { this.setState({ product_name: event.target.value }) }}
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}
                              />
                            </CInputGroup>

                            <label style={{ color: "white" }}>Category</label>
                            <CInputGroup className="mb-3">

                              <CSelect name="cityname" id="select"
                                onChange={(event) => { this.getSubCat(event.target.value) }}
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}>
                                <option>Select Category</option>
                                {this.state.catList.data && this.state.catList.data.length > 0 ?
                                  this.state.catList.data.map((item, i) => {
                                    return <option item={item} value={item.id}>{item.CatEngName}</option>
                                  }) : 'No Categories Available'}
                              </CSelect>
                            </CInputGroup>

                            <label style={{ color: "white" }}>Sub-Category</label>
                            <CInputGroup className="mb-3">

                              <CSelect name="cityname" id="select"
                                onChange={(event) => { this.setState({ subcat_id: event.target.value }) }}
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }}>
                                <option>Select Sub-Category</option>
                                {this.state.subCatList.data && this.state.subCatList.data.length > 0 ?
                                  this.state.subCatList.data.map((item, i) => {
                                    return <option item={item} value={item.id}>{item.SubCatEngName}</option>
                                  }) : 'No city Available'}
                              </CSelect>
                            </CInputGroup>

                            <label style={{ color: "white" }}>Price</label>
                            <CInputGroup className="mb-3" >

                              <CInput type="text" name="price" placeholder="Price" autoComplete="false" value={this.state.product_price}
                                onChange={(event) => { this.setState({ product_price: event.target.value }) }}
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                            </CInputGroup>

                            <label style={{ color: "white" }}>Old Price</label>
                            <CInputGroup className="mb-3" >

                              <CInput type="text" name="oldprice" placeholder="Old Price" autoComplete="false" value={this.state.product_oldprice}
                                onChange={(event) => { this.setState({ product_oldprice: event.target.value }) }}
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                            </CInputGroup>

                            <label style={{ color: "white" }}>Description</label>
                            <CInputGroup className="mb-3">

                              <CInput type="textarea" name="desc" placeholder="desc" autoComplete="false" value={this.state.product_desc}
                                onChange={(event => { this.setState({ product_desc: event.target.value }) })}
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                            </CInputGroup>

                            <label style={{ color: "white" }}>Image</label>
                            <CInputGroup className="mb-3">

                              <CInput type="file" onChange={this.handleImageChange} name="image"
                                style={{ borderRadius: '30px', backgroundColor: '#3B5998', border: '1px solid #3B5998' }} />
                              {/* <input type="file" onChange={this.handleImageChange} name="image"></input> */}
                            </CInputGroup>

                            <CButton type="submit" value="add" block color="primary" fullWidth={true} onClick={() => this.updateProduct()}
                              style={{ backgroundColor: 'orange', color: "black", borderRadius: '20px' }}>save</CButton>
                            <CButton type="submit" value="add" block color="danger" fullWidth={true} onClick={() => this.handleScreen()}
                              style={{ backgroundColor: 'whitesmoke', color: "black", borderRadius: '20px' }}>Cancle</CButton>
                          </CCardBody>
                        </CCard>
        </CCol>
      </CRow>
    </CContainer>
  </div>

</div> :
           
this.state.proscreen ?
<>
<div style={{textAlign:'right'}}><CButton  type="submit" value="add" color="primary" onClick={()=>this.handleAdd()}>Add Product</CButton></div><br/>
  <div className="table">
          <Table>
      <Thead>
        <Tr>
          <Th>Sr.No.</Th>
          <Th>Image</Th>
          <Th>Product</Th>
          <Th>Price</Th>
          <Th>Price Old</Th>
          <Th>Description</Th>
          <Th colspan='2'>Actions</Th>
        </Tr>
      </Thead>
      { this.state.productList.data && this.state.productList.data.length > 0 ?
              this.state.productList.data.map((item,i)=>
      <Tbody>
        <Tr>
          <Th>{i+1}</Th>
          <Td><img src={imgPath+"Vendor_Product_images/"+item.product_image} alt={item.product_image} style={{ width: '50px', height: '40px', marginTop: '-20px', marginBottom: '-20px' }}></img></Td>
          <Td>{item.product_name}</Td>
          <Td>{item.product_price}</Td>
          <Td>{item.product_oldprice}</Td>
          <Td>{item.product_desc}</Td>
          <Td><span style={{ cursor: 'pointer',paddingRight:'2rem' }}><FontAwesomeIcon icon={faPen} style={{ fontSize: "1rem"}} color="black" onClick={()=>this.handleEdit(item.id)} /></span>
          <span variant="outline-light" style={{ cursor: 'pointer' }}><FontAwesomeIcon icon={faTrash} style={{ fontSize: "1rem"}} color="red" onClick={()=>this.handleDelet(item.id)} /></span></Td>
        </Tr>
      </Tbody>):('No Product Found')}
    </Table>

          </div></> :''
           }
           <BlockUI blocking={this.state.blocking} />
          </>
        )
    }
}
